import { Article } from './article.model';
import { Injectable, EventEmitter } from '@angular/core';



@Injectable({
  providedIn: 'root',
})

export class ArticleService {

  private articles: Article[] = [
    new Article("New Article 1", "https://www.google.com", 20),
    new Article("New Article 2", "https://www.google.com", 20),
    new Article("New Article 3", "https://www.google.com", 20),
  ];



  getArticle() {
    return this.articles.slice();
  }

  voteUp(index: number) {
    this.articles[index].votes += 1;

  }
  voteDown(index: number) {
    this.articles[index].votes -= 1;
  }

  addArticle(addArticle: Article): boolean {
    this.articles.push(addArticle);
    console.log(addArticle);
    return false;
  }

  domain(index: number): string {
    try {
      // e.g. http://foo.com/path/to/bar
      const domainAndPath: string = this.articles[index].link.split('//')[1];
      // e.g. foo.com/path/to/bar
      return domainAndPath.split('/')[0];
    } catch (err) {
      return null;
    }
  }

}
